body {
  min-height: calc(100vh - 8vw);
  background-image: linear-gradient(to top left, #370617, #dc2f02);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Roboto, sans-serif;
}

.container {
  z-index: 2;
  max-width: 100em;
  background-color: #f8f9fa;
  border-radius: 10px;
  grid-template: "head head" 8em
                 "list list" minmax(50em, auto)
                 / 1fr;
  margin: 4vw auto;
  display: grid;
  overflow: hidden;
  box-shadow: .5em 4em 6em .5em #343a4033;
}

.nav__bookmarks .nav__btn, .search__btn {
  cursor: pointer;
  width: 7.9em;
  height: 4.2em;
  color: #f8f9facc;
