  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scaleX(0);
}

.nav__list li:hover:before {
  transform-origin: 0;
  transform: scaleX(100%);
}

.nav__list img {
  width: 2em;
}

.nav__list span {
  text-transform: uppercase;
  letter-spacing: .2em;
  font-size: 1em;
  font-weight: 500;
