  font-size: 2em;
  font-style: italic;
  font-weight: 600;
  display: flex;
}

.spinner {
  grid-column: 2;
  justify-content: center;
  display: grid;
}

.spinner svg {
  width: 4em;
  height: 4em;
  fill: #dc2f02;
  animation: 2s linear infinite rotate;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.message, .error {
  text-align: center;
  background-color: #fff;
  grid-area: -26 / 2;
  display: grid;
}

.message svg, .error svg {
  width: 5em;
}

